<template>   
    <ul>
      <li class='listCon' v-for='listInfo in Lists' :key='listInfo.id'>
        <div class='list' @click='$router.push(listInfo.href)'>
          <div class="listImg" :style="{backgroundImage:'url('+listInfo.listImg+')'}"></div>
          <div class='listInfo'>
            <div class='listTitle' v-text='listInfo.listTitle'></div>
            <div class='listName'  v-text='listInfo.listName'></div>
            <div class='listText'  v-text='listInfo.listText'></div>
          </div>
        </div>
      </li>
    </ul>
</template>
<script>
  export default {
    name: '',
    components: {},
    props: ['Lists'],
    data () {
      return {}
    },
    watch: {},
    computed: {},
    mounted () {
      
    },
    methods: {
     
    }
  }
</script>

<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
      .listCon{
        width:6.9rem;
        height:2.8rem;
        margin:0 0.3rem;
        border-bottom: 0.01rem solid rgb(229,229,229);
        .list{
          width:100%;
          height:2rem;
          padding-top:0.5rem;
          display:flex;
          .listImg{
            width:2.6rem;
            height:100%;
            background-position: center center;
            background-size: cover;
            margin-right:0.23rem;
          }
          .listInfo{
            display:flex;
            justify-content: space-between;
            flex:1;
            overflow:hidden;
            flex-direction: column;
            .listTitle{
              font-size:0.36rem;
              font-weight: bold;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            .listName{
              color:#888888;
              font-size:0.28rem;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            .listText{
              font-size:0.28rem;
              line-height:initial;
              color:#555555;
              display: -webkit-box;
              overflow: hidden;
              text-overflow: ellipsis;
              word-wrap: break-word;
              white-space: normal !important;
              -webkit-line-clamp: 2;
              /*! autoprefixer: off */
              -webkit-box-orient: vertical;
              /* autoprefixer: on */
            }
          }
        }
      }
</style>


